import React from "react"
import "./projects.css"
import {Swiper, SwiperSlide} from "swiper/react"
import SwiperCore, {Navigation} from "swiper"
import "swiper/swiper-bundle.min.css"
import data from "./data.js"
SwiperCore.use([Navigation])
function Projects() {
  return (
    <div className="projects" id="projects">
      <h1 className="title">Explore my projects</h1>
        <div className="all">
        <Swiper spaceBetween={0} navigation breakpoints={{
              // when window width is >= 640px
              640: {
                slidesPerView: 1,
              },
              // when window width is >= 768px
              768: {
                slidesPerView: 2,
              },
              // when window width is >= 1200px
              1200: {
                slidesPerView: 3,
              },
            }}>
        {  data.map((project,index) => {
            return (
              <div className="container">
              <SwiperSlide key={project.id} className="item">
              <img className="img" src={project.img} alt=""/>
              <h3 className="name"><a href={project.src}>{project.name}</a></h3>
              <p className="desc">{project.desc}</p>
              </SwiperSlide>
              </div>
            )
          })
        }
        </Swiper>
      </div>
    </div>
  );
}
export default Projects;
