import React from "react"
import "./about.css"
import {Button} from "@material-ui/core";
import me from "./photo.jpg"

function About() {
  return (
    <div className="about" id="about">
      <div className="container" >
     <div className="flexRow flexCol justify-sb align-center">
     <div data-aos="fade-left">
       <div className="meRightImgContainer">
         <img src={me} alt="" />
       </div>
     </div>
       <div data-aos="fade-right">
         <p className="uppercase bold-500 textColor ls-1 mtb-10">
           <span className="primaryColor">Hello,</span> I am Shankey Raheja
         </p>
         <h1 className="textColor ls-1 mtb-10">Front End Developer</h1>
         <p className="font-12 grey mtb-10">JavaScript || React js</p>
         <p className="font-12 grey mtb-10">I am experienced Data Analyst, and self-taught Web developer. In addition, I recently completed my Masters in applied data science. My interest and expertise lies in Web development, data science and machine learning.</p>
         <p className="font-12 grey mtb-10">Apart from work, I am interested in music, movies, travelling, and coffee. </p>
       </div>

     </div>
   </div>
    </div>

  );
}
export default About;
