import React, {useState} from "react"
import logo from "./logo.PNG"
import "./navbar.css"
import {Link} from "react-scroll"

function Navbar() {

  function sleep(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
  }
  const onclick = () => {
    var burgerButton = document.getElementById("burger")

    sleep(1000).then(() => burgerButton.click())
  }
  return (
    <nav className="navbar navbar-expand-lg navbar-light bg-light sticky-top">
  <a className="navbar-brand" href="#"><img className="logo" src={logo} alt=""/></a>
  <button id="burger" className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>
  <div className="collapse navbar-collapse ms-auto" id="navbarSupportedContent">
    <ul className="navbar-nav ms-auto">
      <li className="nav-item active">
      <Link activeClass="active"
        className="nav-link"
          to="home"
          spy={true}
          smooth={true}
          offset={-60}
          duration={100}
     >
                          <div onClick={onclick}>Home</div>
        </Link>

      </li>
      <li className="nav-item" data-toggle="collapse" data-target=".navbar-collapse.show">
        <Link activeClass="active"
          className="nav-link"
            to="about"
            spy={true}
            smooth={true}
            offset={-60}
            duration={100}
       >
                            <div onClick={onclick}>About me</div>
          </Link>
      </li>
      <li className="nav-item">
      <Link activeClass="active"
        className="nav-link"
          to="skills"
          spy={true}
          smooth={true}
          offset={-60}
          duration={100}
                          >
                          <div onClick={onclick}>My Skills</div>
        </Link>
      </li>
      <li className="nav-item">
      <Link activeClass="active"
        className="nav-link"
          to="projects"
          spy={true}
          smooth={true}
          offset={-60}
          duration={100}
                          >
                          <div onClick={onclick}>Projects</div>
        </Link>
      </li>
      <li className="nav-item">
      <Link activeClass="active"
        className="nav-link"
          to="contact"
          spy={true}
          smooth={true}
          offset={-60}
          duration={100}
                          >
                          <div onClick={onclick}>Contact</div>
        </Link>
      </li>
    </ul>
  </div>
</nav>
  );
}

export default Navbar;
