import React from "react"
import "./skills.css"
import skill from "./skill.PNG"

function Skills() {
  return (
    <div className="skills" id="skills">
      <h1 className="title">Expertise</h1>
      <div className="sliding">
      <img className="image" src={skill} alt=""/>
      </div>
    </div>
  );
}
export default Skills;
