import React from "react"
import "./header.css"
import Typed from "react-typed"
function Header() {
  return (
    <div className="header-wraper" id="home">
    <div className="main-info">
      <h1>SHANKEY RAHEJA</h1>
      <div>
      <h4>Areas of Interest</h4>
      <Typed className="typed-text" strings={["Web Design", "Web Development", "Data Science", "Machine Learning"]} typeSpeed={100} backSpeed={60} loop/>
      </div>
      <a href="" className="btn btn-light button">CONTACT ME</a>
    </div>
    </div>

  );
}
export default Header;
